import React from 'react';
import Input from '../../common/Input';

const DateRangePicker = ({ pickupDate, returnDate, onChange, minPickup, minReturn, unavailableRanges = [] }) => {
  return (
    <div className="date-range-picker">
      <Input
        label="Pickup Date"
        type="date"
        name="pickupDate"
        value={pickupDate}
        onChange={onChange}
        min={minPickup || new Date().toISOString().split('T')[0]}
        required
      />
      <Input
        label="Return Date"
        type="date"
        name="returnDate"
        value={returnDate}
        onChange={onChange}
        min={returnDate ? pickupDate : (pickupDate || new Date().toISOString().split('T')[0])}
        required
      />
      {unavailableRanges.length > 0 && (
        <div className="date-range-picker__unavailable" aria-live="polite">
          <p className="text-small text-muted">Currently unavailable ranges for this car:</p>
          <ul>
            {unavailableRanges.map((range) => (
              <li key={`${range.start}-${range.end}`}>
                {range.start} � {range.end}
              </li>
            ))}
          </ul>
        </div>
      )}
    </div>
  );
};

export default DateRangePicker;
